  text-align: center;
  padding-top: 15px;
  padding: 0px;
  margin: 0px;
}

form{
  text-align: center;
}

table.outer{
  border: 3px black;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

table.outer .outerTableCell{
  border-top: black 3px solid;
  border-bottom: black 3px solid;
  border-radius: 7px;
  padding-top: 60px;
  padding-bottom: 60px;
  padding-left: 30px;
  padding-right: 30px;
}

table.outer tr #create{
  padding-right: 50px;
  border-right: black 3px solid;
  background-color: white;
}

table.outer tr #log{
  padding-left: 50px;
  border-left: black 3px solid;
  background-color: white;
}

